<template>
    <div class="container">
       <div class="main__todos">
        <div class="main__todos-item" v-for="todo in todos" :key="todo.id">
            <p>{{ todo.title }}</p>
        </div>
       </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            todos: null
        }
    },
    created() {
        this.getTodos()
    },
    methods: {
        async getTodos() {
            let res = await fetch('https://jsonplaceholder.typicode.com/todos')
            let data = await res.json()
            this.todos = data
        }
    } 
}
</script>